html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.root-background {
  height: 100%;
  width: 100%;
  background: #000000;
  margin-top: -48px;
  overflow-x: hidden;
}

@font-face {
  font-family: "Agustina-Signature";
  src: local("Agustina-Signature"),
    url(./fonts/Agustina-Signature.otf) format("opentype");
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}
.mi-portfolio.mi-portfolio-visible {
  visibility: visible;
  opacity: 1;
}

.mi-portfolio {
  -webkit-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  visibility: hidden;
  opacity: 0;
}

.mi-portfolio-image {
  display: block;
  position: relative;
}

.mi-portfolio-image:before {
  content: "";
  position: absolute;
  left: 15px;
  top: 15px;
  height: calc(100% - 30px);
  width: calc(100% - 30px);
  background: hsla(0, 0%, 100%, 0.9);
  -webkit-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0;
  transform-origin: 0;
}

.mi-portfolio-image ul {
  margin-bottom: 0;
  padding-left: 0;
  position: absolute;
  left: 0;
  top: 50%;
  text-align: center;
  width: 100%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  visibility: hidden;
  opacity: 0;
  margin-top: 30px;
  -webkit-transition: all 0.4s ease-in-out 0.2s;
  transition: all 0.4s ease-in-out 0.2s;
}

.mi-portfolio-image ul li {
  list-style: none;
  display: inline-block;
  margin: 0 7px;
}

.mi-portfolio-image ul li a,
.mi-portfolio-image ul li button {
  display: inline-block;
  height: 45px;
  width: 45px;
  padding: 5px 0;
  vertical-align: middle;
  text-align: center;
  line-height: 1;
  background: #a4acc4;
  color: #fff;
  border: 1px solid #a4acc4;
  border-radius: 100px;
  outline: none;
  font-size: 1.25rem;
  -webkit-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
}

.mi-portfolio-image ul li a svg,
.mi-portfolio-image ul li button svg {
  height: 100%;
}

/*starts*/
* {
  box-sizing: border-box;
}

body {
  font-family: Arial;
  font-size: 17px;
}

.container {
  position: relative;
  width: 300;
  height: 300;
  margin: 0 auto;
}

.container .content {
  position: absolute;
  top: 233px;
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0.5); /* Black background with 0.5 opacity */
  color: #f1f1f1;
  width: 100%;
  padding: 20px 2px;
}
/*ends*/

.pf-img-container {
  position: relative;
  width: 300;
  height: 300;
}
.portfolioImg {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  backface-visibility: hidden;
  background: transparent;
}

.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}
.container:hover .portfolioImg {
}

.containertainer:hover .middle {
  opacity: 1;
  visibility: visible;
}

.text {
  background-color: #4caf50;
  color: white;
  font-size: 16px;
  padding: 16px 32px;
}
